import { Component, OnInit } from "@angular/core";
import { AuthService } from "../../shared/user/auth.service";
import { Router } from "@angular/router";

interface Passwords {
  tempPass: string;
  newPassword: string;
  confirmNewPassword: string;
}

interface Errors {
  userName: boolean;
  newPassword: boolean;
  comparePassword: boolean;
  tempPass: boolean;
}

@Component({
  selector: "app-change-password",
  templateUrl: "./change-password.component.html",
  styleUrls: ["./change-password.component.scss"]
})
export class ChangePasswordComponent implements OnInit {
  public passwords: Passwords;
  public errors: Errors;
  public userName: string;
  public cpType: string;
  public cpMessage: string;

  public showError; // suppress compiler error

  constructor(private authService: AuthService, private router: Router) {
    this.userName = "";
    this.cpType = "";
    this.cpMessage = "";

    this.passwords = {
      tempPass: "",
      newPassword: "",
      confirmNewPassword: ""
    };

    this.errors = {
      userName: false,
      newPassword: false,
      comparePassword: false,
      tempPass: false
    };
  }

  ngOnInit() {}

  comparePasswords() {
    if (
      this.passwords.newPassword !== "" &&
      this.passwords.confirmNewPassword !== "" &&
      this.passwords.confirmNewPassword !== this.passwords.newPassword
    ) {
      return (this.errors.comparePassword = true);
    }
    return (this.errors.comparePassword = false);
  }

  changePassword() {
    this.authService.changePassword(this.passwords.tempPass, this.passwords.newPassword, this.userName);
    const cpt = this.authService.$cpType.subscribe(data => {
      this.cpType = data;
    });
    const cpm = this.authService.$cpMessage.subscribe(data => {
      this.cpMessage = data;
    });
  }

  cancel() {
    this.router.navigate(["/login"]);
  }
}



